import React from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { currentAssets } from './contentful.js';

const AssetsComponent = () => {

	const dispatch = useDispatch();
	const currentApp = useSelector(state => state.app.currentApp);
	const hasData = useSelector(state => state.app.appsList[currentApp].hasData);
	const isOnline = true;//(!navigator.onLine) ? false : true;

	const chooserClick = (e) => {
		/// Nullify the chooser before open so that 'onChange' will fire even if no change...
		e.target.value = null;
	}

	const fileSelected = (e) => {
		if( ! e.target.files[0] ){
			console.log('file selection cancelled');
			dispatch({type: 'CLOSE_SEARCHBAR' });
			return
		}
		dispatch({type: 'OPEN_SEARCHBAR' });
		document.querySelector('.upload > div > div:nth-child(1)').innerHTML = e.target.files[0].name;

		// now user has chance to confirm or cancel upload...
		// handled in SearchbarComponent.
	}



	/// Get the list of what are our current assets...

	/// No internet - abort.
	if( !isOnline ){
		return ( <div id='assets' className=''>No internet connection.</div>)
	}

	/// Fetching data.
	if( ! hasData ){

		/// Fetch data and dispatch response to store.
		(async () => {
				const response = await currentAssets();
				dispatch({ type: 'APP_GOT_DATA', payload: response } );
		})()

		/// While fetching...
		return (<div id='assets' >Loading Assets...</div>)
	}

	/// Request returned error.
	if( hasData === 'Error' ){
		/** TODO: in here - load dummy data? */
		return (<div id='assets' >Error loading content.</div>)
	}

	/// Data OK - populate component content.


	const retrievedArray = hasData.items;
	const sortedItems =  retrievedArray.sort((a, b) => {
		return Date.parse(new Date(b.sys.createdAt)) - Date.parse(new Date(a.sys.createdAt))
	})

	const itemsList = sortedItems.map((item, i) => {
		//console.log(item);

		//if(item.fields.file.contentType.indexOf('image') > -1) {
			////console.log('IMAGE')
		//} else {

			//switch (item.fields.file.contentType){
				//case 'application/pdf':
					////console.log('PDF');
					//break;
				//case (item.fields.file.contentType.indexOf('image') > -1):
					////console.log('IMAGE');
					//break;
				//default:
			//}
		//}

		const juarel = 'https:' + item.fields.file.url
		return (
				<a key={i}  href={juarel}  target="_blank" rel="noopener noreferrer"  >
					<div  className="assetBox" >	{item.fields.file.fileName}	</div>
				</a>
			)
	})

	/// ... and display them in the main area.
	return (
		<div id='assets' className='container-center'>
			<input type="file" style={{display:'none'}} onChange={fileSelected} onClick={chooserClick}/>
			{ itemsList }
		</div>
		)
}

export default AssetsComponent
