import React from 'react';
import { useSelector, useDispatch/*, useStore */} from 'react-redux';
import { doUpload } from './contentful.js';

export const SearchbarComponent = () => {

	const dispatch = useDispatch();
	const currentApp = useSelector(state => state.app.currentApp);
	const hasData = useSelector(state => state.app.appsList[currentApp].hasData);


	const handleSubmit = (e) => {
		e.preventDefault();

		const searchTerm = e.target.children[0].value

		if(searchTerm.length === 0 || searchTerm === '' || searchTerm === 'undefined'){
			return
		}

		e.target.children[0].value = '';
		dispatch({type: 'CLOSE_SEARCHBAR' });

		if(currentApp === 'Hacker News'){
			if(searchTerm === 'tags=front_page'){
				dispatch({type: 'SET_SEARCH_TERM', payload: `?${searchTerm}` });
			} else if(searchTerm === 'tags=story'){
				dispatch({type: 'SET_SEARCH_TERM', payload: `_by_date?tags=story` });
			}	else {
				dispatch({type: 'SET_SEARCH_TERM', payload: `?query=${searchTerm}` });
			}
		}
		if(currentApp === 'reddit'){
			dispatch({type: 'SET_SEARCH_TERM', payload: `${searchTerm}.json` });
		}



		//console.log('STORE:', store.getState(), currentApp);
	}

	//const handleChange = (e) => {
		//console.log(e.target.value);
	//}
	// autocomplete='off'
	// onClick={ handleChange }

	const cancelUpload = () => {
		document.querySelector('.upload > div > div:nth-child(1)').innerHTML = null;
		document.querySelector('#assets input').value = null;
		dispatch({ type: 'CLOSE_SEARCHBAR' });
	}

	const doUploadLocal = async () => {
		const file = document.querySelector('#assets  input').files[0];
		const response = await doUpload(file);

		/// Evaluate response.
		if(response.sys.id){

			const newObj = {sys:response.sys, fields:{file: {fileName:response.fields.file['en-US'].fileName, url: response.fields.file['en-US'].url}}}
			const newArr = [...hasData.items, newObj]
			const newHasData = {...hasData, items: newArr}

			dispatch({ type: 'APP_GOT_DATA', payload: newHasData  });
			cancelUpload();
		} else {
			console.log(response);
			alert('upload failed');
		}

	}


	let item;
	switch (currentApp){
		case 'Assets':
			item = (
				<div className="container upload">

					<div className="waves-effect waves-light white-text"  style={{textAlign: "center",marginRight: "0", padding: "0" }} onClick={doUploadLocal}   >
						<i className="material-icons waves-effect  waves-light">file_upload</i>
					</div>

					<div className="waves-effect white-text" style={{padding:'0'}}>
						<div></div>
					</div>

					<div style={{position: "absolute",  top: "0",marginRight:"0",  right:"0"}}>
						<div className="waves-effect waves-light white-text" onClick={cancelUpload}>Cancel</div>
						<div className="waves-effect waves-light white-text" onClick={doUploadLocal}>OK</div>
					</div>

				</div>
			)
			break
		default:
			item = (
				<div className="container">
					<form onSubmit={ handleSubmit }>
						<input className="searchbar-input" type='text' name='input-search' placeholder="Enter search term"  />
						<button>  <i className="material-icons waves-effect  waves-light">search</i></button>
					</form>
				</div>
			)
	}


//{{<div className="loader"></div>}}
					//<div className='btn-confirm'>Confirm</div>
					//<div className='btn-cancel'>Cancel</div>

//<div>
//<a class="waves-effect waves-light btn-large" href="#">Wave</a>
				//</div>




	const searchOpen = useSelector(state => state.searchbar.opened);
	const classNameA =  searchOpen ? 'search search-open' : 'search'
	const color = useSelector(state => state.app.appsList[currentApp].color);
	const className = `${color} darken-3 search-bar`

	return (
      <div className={classNameA}  >
        <div className={className}>
						{item}
        </div>
      </div>
	)
}

